.collapsible + * {
  display: none;
}

.auto-width {
  width: auto;
  min-width: 50px;
}

.auto-width-flex {
  flex: 0 auto;
}

.light-mode {
  color: #202020;
  --button-color: #d0d0d0;
  --button-hover-color: silver;
  --button-active-color: #a0a0a0;
  --input-color: #303030;
  --input-background-color: #e0e0e0;
  background-color: #e0e0e0;
}

.dark-mode {
  color: #e0e0e0;
  --button-color: #303030;
  --button-hover-color: #404040;
  --button-active-color: #505050;
  --input-color: #e0e0e0;
